.wrapper {
  position: relative;
  overflow: hidden;
  min-height: 100%;
  background: #121212;
}

.container {
  max-width: 1680px;
  margin: 0 auto;
  background: #121212;
}

.master {
  margin-top: 90px;
  margin-bottom: 70px;
  text-align: center;
}
.master img {
  margin: 0 10px;
  max-width: 100%;
  height: auto;
}

.text {
  margin: 0 auto;
  max-width: 900px;
  font-weight: 500;
  font-size: 34px;
  line-height: 60px;
  text-align: center;
  color: #ffffff;
}
