body {
  background: #121212;
}
.wrapper {
  position: relative;
  overflow: hidden;
  min-height: 100%;
  background: #121212;
}

.container {
  max-width: 1680px;
  margin: 0 auto;
  background: #121212;
}
.textBlock {
  display: flex;
  flex-direction: column;
}
.title {
  margin-top: 40px;
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 48px;
  line-height: 60px;
  text-align: center;
  color: #ffffff;
}
.text {
  flex-grow: 1;
  font-weight: 500;
  font-size: 36px;
  line-height: 60px;
  text-align: center;
  color: #ffffff;
}
